@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --background-gray: #4f4f50;
  --prinary-color: #000000;
  --accent: #b19413;
  --header-text: #b19413;
  --accent-2: #9c9a9a; /*--accent-2: #667632;*/
  --shadow: rgba(0, 0, 0, 0.8);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
